import AOS from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";

const Skills = () => {
  useEffect(() => {
    AOS.init();
  }, []);
  return (
    <div>
      <div className="w-full flex flex-col items-center md:items-start">
        <div className="md:w-full md:flex md:mb-20">
          <p
            className="text-6xl md:basis-1/2 text-center mb-5 md:mb-0 md:text-start"
            data-aos="zoom-in"
          >
            HTML
          </p>
          <p
            className="text-6xl md:basis-1/2 text-center mb-5 md:mb-0 md:text-start"
            data-aos="zoom-in"
          >
            CSS
          </p>
        </div>
        <div className="md:w-full md:flex md:mb-20">
          <p
            className="text-6xl text-center mb-5 md:basis-1/2 md:mb-0 md:text-start"
            data-aos="zoom-in"
          >
            TAILWIND CSS
          </p>
          <p
            className="text-6xl text-center mb-5 md:basis-1/2 md:mb-0 md:text-start"
            data-aos="zoom-in"
          >
            JAVASCRIPT
          </p>
        </div>
        <div className="md:w-full md:flex md:mb-20">
          <p
            className="text-6xl text-center mb-5 md:basis-1/2 md:mb-0 md:text-start"
            data-aos="zoom-in"
          >
            REACT
          </p>
          <p
            className="text-6xl text-center mb-5 md:basis-1/2 md:mb-0 md:text-start"
            data-aos="zoom-in"
          >
            NEXTJS
          </p>
        </div>
      </div>
      <hr className=" mt-12 pb-12" />
    </div>
  );
};

export default Skills;
